<template>
  <div class="container">
    <div class="check-menu-warp">
      <div>业务场景：</div>
      <div>
        <el-tag class="tag" @click="selectTag(index)" :type="item.active ? 'danger' : 'info'" v-for="(item,index) in tag" :key="index">{{item.tagName}}</el-tag>
      </div>
    </div>
    <div>
       <div class="api-list" v-for="i in 5" :key="i">
        <div class="list-left">
          <img src="../../images/apiList/icon.png" alt="">
          <div>
            <span>浦望贷</span>通过前端应用发起贷款资格申请后，准实时查询。
          </div>
        </div>
        <button>API列表及订阅</button>
      </div>
      <div class="pagination-wrap">
        <el-pagination
          background
          popper-class="page-select"
          :current-page="1"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tag:[
        {active: false,tagName:'浦望贷'},
        {active: false,tagName:'供应链快贷'},
        {active: false,tagName:'电子账户'},
        {active: false,tagName:'区块链'},
        {active: false,tagName:'担保协议'},
        {active: false,tagName:'担保支付'},
      ]
    }
  },
  methods: {
    selectTag(index) {
      this.tag.forEach((element,i) => {
        if(i !== index){
          element.active = false
        } 
      });
      this.tag[index].active =  !this.tag[index].active
    }
  }
}
</script>

<style lang="less" scoped>
.check-menu-warp {
  background-color: #fff;
  padding: 0 29px;
  height: 137px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  margin: 27px 0 29px 0;
  > div:last-child {
    margin-left: 25px;
    .tag {
      margin-right: 10px;
      cursor: pointer;
    }
  }
}
.api-list {
  display: flex;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 81px;
  padding: 0 34px 0 56px;
  box-sizing: border-box;
  margin-bottom: 13px;
  img{
    width: 57px;
    display: block;
    margin-right: 49px;
  }
  .list-left {
    display: flex;
    align-items: center;
    > div {
      font-size: 16px;
      color: #676767;
      span {
        color: #000000;
        font-weight: 500;
        margin-right: 16px;
      }
    }
  }
  > button {
    width: 158px;
    height: 39px;
    line-height: 39px;
    text-align: center;
    color: #fff;
    font-size: 15px;
    font-weight: 500;
    background: #B90909;
    border-radius: 4px;
    border: 0;
    display: block;
    cursor: pointer;

  }
}
.pagination-wrap {
  /*带背景的分页按钮背景色begin*/
  // border: 1px solid #000;
  text-align: right;
  margin-top: 8px;
  margin-bottom: 15px;
  /deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
    background-color: #C00B0B;
    color: #FFF;
  }
  /deep/ .el-pagination.is-background .el-pager li.active {
    color: #fff;
    cursor: default;
  }
  /deep/ .el-pagination.is-background .el-pager li:hover {
    color: #C00B0B;
  }
  /deep/ .el-pagination.is-background .el-pager li:not(.disabled):hover {
    color: #C00B0B;
  }
  /deep/ .el-pagination.is-background .el-pager li:not(.disabled).active:hover {
    background-color: #C00B0B;
    color: #FFF;
  }
  /*带背景的分页按钮背景色end*/
}

</style>
<style>
.page-select .el-select-dropdown__item.selected {
  color: red!important;
} 
</style>


